<template>
    <div class="home border-1px">
        <v-header :seller='seller'></v-header>
        <div class="tab border-1px">
          <div class="tab-item">
            <router-link :to="{ name: 'goods'}" exact>
                
            </router-link>
          </div>
          <div class="tab-item">
            <router-link :to="{ name: 'ratings'}">
                
            </router-link>
          </div>
          <div class="tab-item">
            <router-link :to="{ name: 'seller'}">
                
            </router-link>
          </div>
        </div>
        <router-view :seller='seller'></router-view>
    </div>
</template>
<script>
  import {urlParse} from '../common/js/util';
  import header from 'components/header/header';
  const ERR_OK = 0;
  export default {
      name: 'home',
      data() {
          return {
              seller: {
                  id: (() => {
                      let queryParam = urlParse();
                      return queryParam.id;
                  })()
              }
          };
      },
      created() {
      this.$http.get('https://github.com/mangogit/mangogit.github.io/tree/master/elem/seller.json?id=' + this.seller.id).then((response) => {
        response = response.body;
        if (response.errno === ERR_OK) {
          this.seller = Object.assign({}, this.seller, response.data);
        }
      });
    },
      components: {
          'v-header': header
      }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../common/stylus/mixin.styl" 
  .tab
    display: flex
    width: 100%
    height: 40px
    line-height: 40px
    border-1px(rgba(7, 17, 27, 0.1))
    .tab-item
      flex: 1
      text-align: center
      & > a
        display: block
        font-size: 14px
        color: rgb(77, 85, 93)
        &.active
          color: rgb(240, 20, 20)

</style>



